<template>
  <div class="pages"></div>
</template>

<script>
import { _isMobile } from "@/utils/isMobile.js";
export default {
  computed: {
    device_type() {
      return this.$store.getters.device_type;
    },
  },
  methods: {
    _ifIsMobile() {
      this.$router.replace({ name: `${this.device_type}` });
    },
  },
  created() {
    this.$nextTick(() => {
      this._ifIsMobile();
    });
  },
};
</script>


